.mainSlot{
    width: 800px;
    height: 600px;
    margin: 50px 350px;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.slotRow{
    width: 800px;
    height: 180px;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
}
.slotCol{
    border: 1px solid black;
    width: 220px;
    height: 180px;
}
img{
    width: 218px;
    height: 180px;
}
.spin{
    width: 150px;
    height: 50px;
}
button{
    background-color: rgb(9, 1, 19);
    color: white;
    font-size: 25px;
    margin-bottom: 50px;
}